import { createReducer, on } from '@ngrx/store';

import { IViewDefinition } from '@helix/platform/view/api';
import * as ViewDesignerActions from './view-designer.actions';

export function getInitialState(): IViewDefinition {
  return null;
}

export const reducer = createReducer<IViewDefinition>(
  getInitialState(),

  on(ViewDesignerActions.initViewDesigner, getInitialState),

  on(ViewDesignerActions.destroyViewDesigner, getInitialState),

  on(ViewDesignerActions.setGeneratedViewDefinition, (state, { payload }) => {
    return payload;
  })
);

export function viewDefinitionReducer(state, action) {
  return reducer(state, action);
}
